import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';

import AccessTime from "@material-ui/icons/AccessTime";
import Delete from "@material-ui/icons/Delete";
import Save from "@material-ui/icons/Save";
import Scale from "@material-ui/icons/LinearScale";
import Edit from "@material-ui/icons/Edit";

import PlayCircleOutline from "@material-ui/icons/PlayCircleOutline";


// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsDialogPlayer from "components/@NBrootsDialogPlayer/NBrootsDialogPlayer.js";

import styles from "assets/jss/material-dashboard-pro-react/views/vediosListStyle.js";

import gUid from "utils/functions/gUid.js"

import { dataTable } from "variables/vedio_list.js"

import verifyEmail from "utils/validations/verifyEmail"
import verifyLength from "utils/validations/verifyLength"

const useStyles = makeStyles(styles);

export default function VediosList() {

  const classes = useStyles();

  const [isLoading, setIsLoading] = React.useState(false);

  //playerInfo [表示FLG、VedioSrc]    
  const [playerInfo, setPlayerInfo] = React.useState([false,'']);

  //ページジャンプとソート用
  const [count, setCount] = React.useState(0);
  const [pageSize, setPageSize] = React.useState(10);
  const [pageNo, setPageNo] = React.useState(1);
  // const [orderBy, setOrderBy] = React.useState("");
  // const [sortType, setSortType] = React.useState("");

  //明細情報
  const [lstContent, setLstContent] = React.useState([]);

  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {

    setIsLoading(true);

    formatLstData().then((lst) => {

      setLstContent(lst);

      setIsLoading(false);

    });


  }, []);    //[] 忘れないよう…　初期化



  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {

    var t1 = dataTable.count;
    var t2 = dataTable.pageNo;
    var t3 = dataTable.pageSize;

    setCount(t1);
    setPageNo(t2);
    setPageSize(t3);

    var lst = dataTable.dataRows;

    return Promise.all(

      lst.map(item => {
        return {
          ...item,
          //追加属性
          isEdit: false,
          chkResultName: '',
        }
      }
    ));
    
  };

  //改ページ処理
  const handleChangePageStart = (page) =>{

    if(page.disabled) return;
    if(page.text === pageNo) return;

    setIsLoading(true);

    setTimeout(() => {

      if(page.text === 'Prev'){
        setPageNo(pageNo - 1);
      }else if(page.text === 'Next'){
        setPageNo(pageNo + 1 );
      }else{
        setPageNo(page.text);
      }

      setIsLoading(false);

    }, 500);
  }

  //画面動作 EDIT
  const handleClickStartEdit = (obj) => {

    var index = lstContent.indexOf(obj);

    if (obj.isEdit) {
      obj.isEdit = false;
    } else {
      obj.isEdit = true;
    }
    lstContent[index] = obj;

    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面入力チェック結果
  const setChkResult = (obj, propname, inState) => {

    var index = lstContent.indexOf(obj);

    lstContent[index][propname] = inState;

    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面動作 Edit On Change
  const handleChangeStartEdit = (obj, propname, inVal) => {

    var index = lstContent.indexOf(obj);

    lstContent[index][propname] = inVal;
    //！！！　Array　展開方式 ！！！
    setLstContent([...lstContent]);

  };

  //画面動作 SAVE
  const handleClickStartSave = (obj) => {

    var index = lstContent.indexOf(obj);

    setIsLoading(true);

    setTimeout(() => {

      if (obj.isEdit) {
        obj.isEdit = false;
      } else {
        obj.isEdit = true;
      }
      lstContent[index] = obj;

      //！！！　Array　展開方式 ！！！
      setLstContent([...lstContent]);

      setIsLoading(false);

    }, 1000);


  };

  //画面動作 ADD
  const handleClickStartAdd = () => {
    var item =
    {
      id: gUid(),
      src: "/img/VedioMarket/1.mp4",
      poster: "/img/VedioMarket/1.png",
      name: "11.mp4",
      detail: "映像内容、説明",
      size: "110M",
      length: "20S",
      //追加属性
      isEdit: false,
      chkResultName: '',
    };

    setIsLoading(true);

    setTimeout(() => {

      //！！！　Array　展開方式 ！！！
      setLstContent([...lstContent, item]);

      setIsLoading(false);

    }, 1000);

  };

  //画面動作 PLAY
  const handleClickStartPlay = (obj) => {

    setPlayerInfo([true,obj.src]);

  };

  //画面動作 DEL
  const handleClickStartDel = (obj) => {

    var index = lstContent.indexOf(obj);


    setIsLoading(true);

    setTimeout(() => {

      lstContent.splice(index, 1);
      //！！！　Array　展開方式 ！！！
      setLstContent([...lstContent]);

      setIsLoading(false);

    }, 1000);


  };

  return (
    <Box>

      <Grid container spacing={1}>
        {/* 検索部 */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>

        </Grid>
        {/* 操作ボタン部 */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Button color="warning" round onClick={() => handleClickStartAdd()}>新しい映像</Button>
        </Grid>
        {/* ページジャンプ */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          <NBrootsPagination
            count     = {count}
            pagesize  = {pageSize}
            current   = {pageNo}
            onClick   = {handleChangePageStart}
            color     = "warning"
          />
        </Grid>
      </Grid>
      {/* ScrollBar表示部分 */}
      <Box xs={12} className={classes.gidBox}>        
        <GridContainer>
          {lstContent.map((item, idx) =>
            <GridItem xs={12} sm={6} md={3} key={idx}>
              <Card product className={classes.cardHover}>
                <CardHeader image className={classes.cardHeaderHover}>
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                    <img src={item.poster} alt="..." />
                  </a>
                </CardHeader>
                <CardBody className={classes.cardBody}>
                  <div className={classes.cardHoverUnder}>
                    <Tooltip id="tooltip-top" title="プレイ" placement="bottom"
                      classes={{ tooltip: classes.tooltip }}>
                      <Button color="transparent" simple justIcon>
                        <PlayCircleOutline className={classes.underChartIcons}
                          onClick={() => handleClickStartPlay(item)} />
                      </Button>
                    </Tooltip>
                    {item.isEdit ?
                      <Tooltip id="tooltip-top" title="保存" placement="bottom"
                        classes={{ tooltip: classes.tooltip }}>
                        <Button color="success" simple justIcon>
                          <Save className={classes.underChartIcons}
                            onClick={() => handleClickStartSave(item)} />
                        </Button>
                      </Tooltip>
                      :
                      <Tooltip id="tooltip-top" title="編集" placement="bottom"
                        classes={{ tooltip: classes.tooltip }}>
                        <Button color="success" simple justIcon>
                          <Edit className={classes.underChartIcons}
                            onClick={() => handleClickStartEdit(item)} />
                        </Button>
                      </Tooltip>
                    }
                    <Tooltip id="tooltip-top" title="削除" placement="bottom"
                      classes={{ tooltip: classes.tooltip }}>
                      <Button color="danger" simple justIcon>
                        <Delete className={classes.underChartIcons}
                          onClick={() => handleClickStartDel(item)} />
                      </Button>
                    </Tooltip>
                  </div>
                  {item.isEdit ?
                    <CustomInput id="name"
                      success={item.chkResultName === "success"}      // chkResult + Json.prop
                      error={item.chkResultName === "error"}
                      formControlProps={{
                        className:classes.inputName,
                        fullWidth: true
                      }}
                      value={item.name}                               //set
                      inputProps={{
                        defaultValue: item.name,                      //set
                        onChange: event => {
                          if (verifyEmail(event.target.value) && verifyLength(event.target.value, 20)) {
                            setChkResult(item, "chkResultName", "success");
                          } else {
                            setChkResult(item, "chkResultName", "error");
                          }
                          handleChangeStartEdit(item, "name", event.target.value);
                        },
                      }}
                    />
                    :
                    <h4 className={classes.cardProductTitle}>
                      {item.name}
                    </h4>
                  }
                </CardBody>
                <CardFooter product>
                  <div className={classes.price}>
                    <Scale /><p>{item.size}</p>
                  </div>
                  <div className={classes.price}>
                    <AccessTime /><p>{item.length}</p>
                  </div>
                </CardFooter>
              </Card>
            </GridItem>
          )}
        </GridContainer>
      </Box>
      <NBrootsDialogPlayer open={playerInfo[0]} src={playerInfo[1]} onClose={()=>setPlayerInfo([false,''])}/>
      <NBrootsDialogProcess open={isLoading} color="warning" />
    </Box>
  );
}
