import React,{useState} from "react";
import ReactTable from "react-table";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
// @material-ui/icons
import FiberManualRecord from "@material-ui/icons/FiberManualRecord";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Button from "components/CustomButtons/Button.js";

import { dataTable } from "variables/playpopupdata.js";

import priceImage1 from "assets/img/card-1.jpeg";
// import priceImage2 from "assets/img/card-2.jpeg";

import styles from "assets/jss/material-dashboard-pro-react/views/playListStyle.js";

const defaultButton = props => (
  <Button color="warning" {...props} round >
  &nbsp;&nbsp;&nbsp;&nbsp;{props.children}&nbsp;&nbsp;&nbsp;&nbsp;
 </Button>
)

const useStyles = makeStyles(styles);

export default function MarketRadioPopup() {
  const classes = useStyles();
  const [expanded,setExpanded] = useState();
  const [selectedValue, setSelectedValue] = React.useState('a');
  const handleChange = event => {
    setSelectedValue(event.target.value);
  };

  const [data, setData] = React.useState(
    dataTable.dataRowsForRadio.map((prop, key) => {
      return {
        id: key,
        no: prop[0],
        movie: prop[1],
        playTime: prop[2],
        size: prop[3],
        
        actions: (
          // we've added some custom button actions
          <div className="actions-right">
            <div className={classes.checkboxAndRadio +" " +classes.checkboxAndRadioHorizontal}>
              <FormControlLabel
                control={
                  <Radio
                    checked={selectedValue === prop[4]}
                    onChange={handleChange}
                    value={prop[4]}
                    name="radio button demo"
                    aria-label={prop[4]}
                    icon={
                      <FiberManualRecord
                        className={classes.radioUnchecked}
                      />
                    }
                    checkedIcon={
                      <FiberManualRecord
                        className={classes.radioChecked}
                      />
                    }
                    classes={{
                      checked: classes.radio,
                      root: classes.radioRoot
                    }}
                  />
                }
                classes={{
                  label: classes.label,
                  root: classes.labelRoot
                }}
                label=""
              />
            </div>
          </div>
        )
      };
    })
  );
  const defaultExpandedRows = data.map((element, index) => {return {index: true}});
  const imageShow = props =>(
    <div>
      <Tooltip id="tooltip-top" title="映像1.mp4" placement="bottom" classes={{ tooltip: classes.tooltip }}>
        <img src={priceImage1} alt="..." width="20%" height="20%"/>
      </Tooltip>
    </div>
  );
  return (
    <div>
      <GridContainer>
        <GridItem xs={12}>
          <Card>
            <CardBody>
              <ReactTable
                data={data}
                columns={[
                  {
                    Header: "NO",
                    accessor: "no",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "映像",
                    accessor: "movie",
                    Cell:imageShow,
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "プレイ時間",
                    accessor: "playTime",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "サイズ",
                    accessor: "size",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "",
                    accessor: "actions",
                    sortable: false,
                    filterable: false
                  }
                ]}
                expanded={expanded}
                defaultPageSize={10}
                showPaginationTop
                showPaginationBottom={false}
                className="-striped -highlight"
                filterable={false}
                previousText="前"
                nextText="次"
                pageText=""
                ofText="/"
                rowsText = "行"
                PreviousComponent = {defaultButton}
                NextComponent = {defaultButton}
                defaultExpanded={defaultExpandedRows}
              />
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}