import React,{useState} from "react";
import ReactTable from "react-table";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
// @material-ui/icons
import Check from "@material-ui/icons/Check";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Button from "components/CustomButtons/Button.js";

import { dataTable } from "variables/playpopupdata.js";

import priceImage1 from "assets/img/card-1.jpeg";
// import priceImage2 from "assets/img/card-2.jpeg";

import styles from "assets/jss/material-dashboard-pro-react/views/playListStyle.js";

const defaultButton = props => (
  <Button color="warning" {...props} round >
  &nbsp;&nbsp;&nbsp;&nbsp;{props.children}&nbsp;&nbsp;&nbsp;&nbsp;
 </Button>
)

const useStyles = makeStyles(styles);

export default function MarketPopup() {
  const classes = useStyles();
  const [expanded,setExpanded] = useState();
  const [checked, setChecked] = React.useState([24, 22]);
  const handleToggle = value => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
  };
  const [data, setData] = React.useState(
    dataTable.dataRows.map((prop, key) => {
      return {
        id: key,
        no: prop[0],
        movie: prop[1],
        playTime: prop[2],
        size: prop[3],
        actions: (
          // we've added some custom button actions
          <div className="actions-right">
            <FormControlLabel
                control={
                  <Checkbox
                    tabIndex={-1}
                    onClick={() => handleToggle(2)}
                    checkedIcon={<Check className={classes.checkedIcon} />}
                    icon={<Check className={classes.uncheckedIcon} />}
                    classes={{
                      checked: classes.checked,
                      root: classes.checkRoot
                    }}
                  />
                }
                classes={{
                  label: classes.label,
                  root: classes.labelRoot
                }}
                label=""
              />
          </div>
        )
      };
    })
  );
  const defaultExpandedRows = data.map((element, index) => {return {index: true}});
  const imageShow = props =>(
    <div>
      <Tooltip id="tooltip-top" title="映像1.mp4" placement="bottom" classes={{ tooltip: classes.tooltip }}>
        <img src={priceImage1} alt="..." width="20%" height="20%"/>
      </Tooltip>
    </div>
  );
  return (
    <div>
      <GridContainer>
        <GridItem xs={12}>
          <Card>
            <CardBody>
              <ReactTable
                data={data}
                columns={[
                  {
                    Header: "NO",
                    accessor: "no",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "映像",
                    accessor: "movie",
                    Cell:imageShow,
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "プレイ時間",
                    accessor: "playTime",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "サイズ",
                    accessor: "size",
                    sortable: false,
                    filterable: false
                  },
                  {
                    Header: "",
                    accessor: "actions",
                    sortable: false,
                    filterable: false
                  }
                ]}
                expanded={expanded}
                defaultPageSize={10}
                showPaginationTop
                showPaginationBottom={false}
                className="-striped -highlight"
                filterable={false}
                previousText="前"
                nextText="次"
                pageText=""
                ofText="/"
                rowsText = "行"
                PreviousComponent = {defaultButton}
                NextComponent = {defaultButton}
                defaultExpanded={defaultExpandedRows}
              />
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}