/* eslint-disable */
import React from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Check from "@material-ui/icons/Check";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Tooltip from "@material-ui/core/Tooltip";
// @material-ui/icons
import Dvr from "@material-ui/icons/Dvr";
import Delete from "@material-ui/icons/Delete";
import Close from "@material-ui/icons/Close";
import Fiber from "@material-ui/icons/FiberManualRecord";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Button from "components/CustomButtons/Button.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import { dataTable } from "variables/userdata.js";

import styles from "assets/jss/material-dashboard-pro-react/views/userListStyle.js";

const defaultButton = props => (
  <Button color="warning" {...props} round >
  &nbsp;&nbsp;&nbsp;&nbsp;{props.children}&nbsp;&nbsp;&nbsp;&nbsp;
 </Button>
)

const useStyles = makeStyles(styles);

export default function UserList() {
  const classes = useStyles();
  const AuthorityIcon = props => (
    <Button 
      justIcon 
      round 
      simple
      color="info"
      className="edit"
    >
      <Fiber/>
    </Button>
  )
  const [data, setData] = React.useState(
    dataTable.dataRows.map((prop, key) => {
      return {
        id: key,
        userNo: prop[0],
        emailAddress: prop[1],
        userName: prop[2],
        furikana: prop[3],
        password: prop[4],
        authority: prop[5],
        actions: (
          // we've added some custom button actions
          <div className="actions-right">
            {/* use this button to add a edit kind of action */}
            <Tooltip
              id="tooltip-top"
              title="変更"
              placement="bottom"
              classes={{ tooltip: classes.tooltip }}
            >
              <Button
                justIcon
                round
                simple
                onClick={() => {
                  let obj = data.find(o => o.id === key);
                  handleClickEditDialogOpen();
                }}
                color="warning"
                className="edit"
              >
                <Dvr />
              </Button>
            </Tooltip>{" "}
            {/* use this button to remove the data row */}
            <Tooltip
              id="tooltip-top"
              title="削除"
              placement="bottom"
              classes={{ tooltip: classes.tooltip }}
            >
              <Button
                justIcon
                round
                simple
                onClick={() => {
                  var newData = data;
                  newData.find((o, i) => {
                    if (o.id === key) {
                      // here you should add some custom code so you can delete the data
                      // from this component and from your server as well
                      newData.splice(i, 1);
                      return true;
                    }
                    return false;
                  });
                  setData([...newData]);
                }}
                color="danger"
                className="remove"
              >
                <Delete />
              </Button>
            </Tooltip>{" "}
          </div>
        )
      };
    })
  );
  const [addDialogOpen, setAddDialogOpen] = React.useState(false);

  const handleClickAddDialogOpen = () => {
    setAddDialogOpen(true);
  };
  const handleClickAddDialogClose = () => {
    setAddDialogOpen(false);
  };
  const [editDialogOpen, setEditDialogOpen] = React.useState(false);

  const handleClickEditDialogOpen = () => {
    setEditDialogOpen(true);
  };
  const handleClickEditDialogClose = () => {
    setEditDialogOpen(false);
  };
  const defaultExpandedRows = data.map((element, index) => {return {index: true}});
  const [checked, setChecked] = React.useState([24, 22]);
  const handleToggle = value => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
  };
  return (
    <div>
    <Button color="info" round onClick={handleClickAddDialogOpen}>アカウント追加</Button>
      <br />
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardBody>
            <ReactTable
              data={data}
              filterable
              columns={[
                {
                  Header: "NO",
                  accessor: "userNo"
                },
                {
                  Header: "メールアドレス",
                  accessor: "emailAddress"
                },
                {
                  Header: "ユーザ名",
                  accessor: "userName"
                },
                {
                  Header: "フリカナ",
                  accessor: "furikana"
                },
                {
                  Header: "パスワード",
                  accessor: "password",
                  sortable: false
                },
                {
                  Header: "管理者",
                  accessor: "authority",
                  Cell: AuthorityIcon,
                  sortable: false
                },
                {
                  Header: "",
                  accessor: "actions",
                  sortable: false,
                  filterable: false
                }
              ]}
              
              defaultPageSize={10}
              showPaginationTop
              showPaginationBottom={false}
              className="-striped -highlight"
              filterable={false}
              previousText="前"
              nextText="次"
              pageText=""
              ofText="/"
              rowsText = "行"
              PreviousComponent = {defaultButton}
              NextComponent = {defaultButton}
              defaultExpanded={defaultExpandedRows}
            />
          </CardBody>
        </Card>
      </GridItem>
      <Dialog onClose={handleClickAddDialogClose} aria-labelledby="customized-dialog-title" open={addDialogOpen}>
        <DialogTitle id="customized-dialog-title" className={classes.dialogHeader} onClose={handleClickAddDialogClose}>
          アカウント追加
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="warning"
            onClick={handleClickAddDialogClose}
            style={{ float: "right" }}
          >
            <Close className={classes.modalClose} />
          </Button>
        </DialogTitle>
        <DialogContent>
          <GridItem xs={12} sm={12}>
            <CustomInput
              labelText="メールアドレス *"
              id="emailAddress"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="ユーザ名 *"
              id="userName"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="フリカナ *"
              id="furikana"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="パスワード *"
              id="password"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <div className={classes.checkboxAndRadio}>
              <FormControlLabel
                control={
                  <Checkbox
                    tabIndex={-1}
                    onClick={() => handleToggle(2)}
                    checkedIcon={<Check className={classes.checkedIcon} />}
                    icon={<Check className={classes.uncheckedIcon} />}
                    classes={{
                      checked: classes.checked,
                      root: classes.checkRoot
                    }}
                  />
                }
                classes={{
                  label: classes.label,
                  root: classes.labelRoot
                }}
                label="管理者"
              />
            </div>
          </GridItem>
        </DialogContent>
        <DialogActions>
          <Button round onClick={handleClickAddDialogClose} color="warning">
            キャンセル
          </Button>
          <Button round onClick={handleClickAddDialogClose} color="warning">
            保存
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog onClose={handleClickEditDialogClose} aria-labelledby="customized-dialog-title" open={editDialogOpen}>
        <DialogTitle id="customized-dialog-title" className={classes.dialogHeader} onClose={handleClickEditDialogClose}>
          アカウント変更
          <Button
            justIcon
            className={classes.modalCloseButton}
            key="close"
            aria-label="Close"
            color="warning"
            onClick={handleClickEditDialogClose}
            style={{ float: "right" }}
          >
            <Close className={classes.modalClose} />
          </Button>
        </DialogTitle>
        <DialogContent>
          <GridItem xs={12} sm={12}>
            <CustomInput
              labelText="メールアドレス *"
              id="emailAddress"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="ユーザ名 *"
              id="userName"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="フリカナ *"
              id="furikana"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <CustomInput
              labelText="パスワード *"
              id="password"
              formControlProps={{ fullWidth: true }}
              inputProps={{ type: "text",autoComplete: "off" }}
            />
            <div className={classes.checkboxAndRadio}>
              <FormControlLabel
                control={
                  <Checkbox
                    tabIndex={-1}
                    onClick={() => handleToggle(2)}
                    checkedIcon={<Check className={classes.checkedIcon} />}
                    icon={<Check className={classes.uncheckedIcon} />}
                    classes={{
                      checked: classes.checked,
                      root: classes.checkRoot
                    }}
                  />
                }
                classes={{
                  label: classes.label,
                  root: classes.labelRoot
                }}
                label="管理者"
              />
            </div>
          </GridItem>
        </DialogContent>
        <DialogActions>
          <Button round onClick={handleClickEditDialogClose} color="warning">
            キャンセル
          </Button>
          <Button round onClick={handleClickEditDialogClose} color="warning">
            変更
          </Button>
        </DialogActions>
      </Dialog>
    </GridContainer>
    </div>
  );
}
